    isEquivalent(a, b) {
        // Create arrays of property names
        var aProps = Object.getOwnPropertyNames(a);
        var bProps = Object.getOwnPropertyNames(b);
        // If number of properties is different,
        // objects are not equivalent
        if (aProps.length != bProps.length) {
            return false;
        }
        for (var i = 0; i < aProps.length; i++) {
            var propName = aProps[i];
            // If values of same property are not equal,
            // objects are not equivalent
            if (a[propName] !== b[propName]) {
                return false;
            }
        }
        // If we made it this far, objects
        // are considered equivalent
        return true;
    }
    AddItem(Item, Index = -1) {
        let bAdd = true;
        let str = "", strKey = "";
        let objLru;
        if (this.m_lstCopyList != null) {
            this.m_lstCopyList = new Vector();
        }
        //
        // Primero ver si hay clave
        if ("NULL".equals((strKey = Item.GetObjectIdString())))
            strKey = null;
        //
        // Si ahora no la tiene, jodo est el pobre
        if (!StringUtils.IsEmptyString(strKey)) {
            // Tiene clave
            // Buscar si ya este objeto est en la coleccin
            if (this.m_lstObjectList.containsKey(strKey)) {
                // Ya hay un objeto con esta clave
                let old = this.m_lstObjectList.get(strKey);
                if (old != null)
                    if ((bAdd = !this.isEquivalent(old, Item))) {
                        if (this.m_lstOrderedList.contains(old))
                            this.m_lstOrderedList.remove(old);
                        if (this.m_lstObjectLru.contains(old))
                            this.m_lstObjectLru.removeElement(old);
                    }
                    else {
                        return true;
                    }
                // Comprobar si es este mismo
                //bAdd = false; // Es diferente
            } // Ya hay un objeto con esta clave
            if (bAdd) {
                // Hay que adicionar
                this.m_lstObjectList.put(strKey, Item);
            } // Hay que adicionar
        } // Tiene clave
        // Si la coleccin tiene lmite, tenemos que cargarnos el objeto menos recientemente
        // accedido, de manera que el nuevo pase a ser el ms recientemente usado...
        if (this.Options.nThreshold > 0) {
            // Ver si se ha sobrepasado el lmite
            if (this.m_lstObjectLru.contains(Item))
                this.m_lstObjectLru.removeElement(Item); // Existe en el LRU, eliminarlo de la lista
            // Ahora buscar el menos recientemente usado y sacarlo de la lista
            if (this.m_lstOrderedList.length >= this.Options.nThreshold) {
                // Buscar el menos recientemente usado
                objLru = this.m_lstObjectLru[0]; // El primero
                if (!StringUtils.IsEmptyString((str = objLru.GetObjectIdString()))) {
                    // Si est en el hash, nos lo cargamos
                    if (this.m_lstObjectList.containsKey(str))
                        this.m_lstObjectList.delete(str);
                } // Si est en el hash, nos lo cargamos
                // Ahora nos lo cargamos de la lista
                this.m_lstOrderedList.remove(0);
                // Y del LRU
                this.m_lstObjectLru.removeElement(objLru);
            } // Buscar el menos recientemente usado
        } // Ver si se ha sobrepasado el lmite
        if (Index >= 0) {
            // Tiene ndice
            if (this.m_lstOrderedList.length > Index)
                this.m_lstOrderedList.add(Index, Item);
            else
                this.m_lstOrderedList.push(Item);
        } // Tiene ndice
        else {
            // Adicionar a la lista
            // Si no est en lista, lo adicionamos
            if (!this.m_lstOrderedList.contains(Item))
                this.m_lstOrderedList.push(Item);
        } // Adicionar a la lista
        // Si hay LRU, adicionar al final
        if (this.Options.nThreshold > 0)
            this.m_lstObjectLru.push(Item); // Adicionar este al final
        // Si se ha adicionado, actualizar el BL solo si es especial
        if (this.m_bIsSpecial)
            this.m_nBrowseLength = this.m_lstOrderedList.length;
        // Completo
        return true;
    }
    /**
     * Devuelve el ndice de un objeto dentro de la coleccin
     * @param Item			Objeto cuyo ndice se quiere conocer.
     * @return				Devuelve el ndice del objeto o -1 si no est en la lista.
     */
    ObjectIndex(Item) {
        // Si no est, no est...
        if (!this.m_lstOrderedList.contains(Item))
            return -1;
        for (let i = 0; i < this.m_lstOrderedList.length; i++)
            if (this.m_lstOrderedList[i] === Item)
                return i;
        // Completo
        return -1;
    }
    GetObjectInt(Index) {
        // Devolver el elemento indicado por Index
        if (this.m_lstOrderedList.length <= Index)
            return null;
        if (Index < 0)
            Index = 0;
        return this.m_lstOrderedList[Index];
    }
